@import url(https://fonts.googleapis.com/css?family=Lobster|Source+Code+Pro:400,500);
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  min-height: 100%;
  height: 100%;
  font-family: 'Source Code Pro', monospace;
  font-weight: 500;
}

#root {
  height: 100%;
}

.skills {
  position: relative;
  display: flex;
}

.skills-left {
  display: flex;
  flex-direction: column;
  margin-right: 10px;
  text-align: right;
}

.skills-right {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
}

.skill {
  font-size: 1rem;
}

.split {
  position: absolute;
  top: 70px;
  left: 6px;
  width: 200px;
  height: 2px;
  border: 0;
  background-image: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(0, 0, 0, 0)),
    color-stop(rgba(0, 0, 0, 0.9)),
    to(rgba(0, 0, 0, 0))
  );
  background-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.9),
    rgba(0, 0, 0, 0)
  );
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}

/*
.platforms {
  display: flex;
  justify-content: center;
  font-size: 3rem;
} */

.platform-icon {
  margin: 20px 10px 0 10px;
  font-size: 3rem;
}

.platform-icon:visited {
  color: #000;
}

.project {
  width: 80%;
  min-width: 300px;
  border-radius: 10px;
  margin-top: 50px;
  background: #fcf3ca;
}

.project-title {
  margin-left: 20px;
}

.project:last-of-type {
  margin-bottom: 50px;
}

.project-img {
  width: 100%;
  border-top: 1px solid darkgray;
  border-bottom: 1px solid darkgray;
}

.project-description {
  padding: 0 20px;
  margin: 20px 0 0 0;
}

.project-link-container {
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.project-links {
  display: flex;
  justify-content: space-evenly;
}

.project-link-name {
  margin: 0;
  margin-top: 15px;
  font-weight: 600;
}

.project-link {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  border: none;
  margin: 10px 20px 10px 20px;
  color: inherit;
  text-decoration: none;
  cursor: pointer;
  background-color: #c4eada;
  box-shadow: 4px 4px 0 0 #ef7b7b;
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
}

.project-link:hover {
  -webkit-transform: translateY(4px) translateX(4px);
          transform: translateY(4px) translateX(4px);
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
  background-color: #ef7b7b;
  box-shadow: -4px -4px 0 0 #c4eada;
}

.project-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App {
  background: #fcf3ca;
}

.left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 4rem 0 .5rem 0;
}

.fixed {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 1.5rem;
}

.logo {
  width: 150px;
  height: 150px;
  border: 3px solid #ef7b7b;
  border-radius: 50% 0 50% 50%;
  background: #c4eada;
  -webkit-transform: scale(1) rotate(-45deg) translateX(0px) translateY(0px)
    skewX(1deg) skewY(1deg);
  transform: scale(1) rotate(-45deg) translateX(0px) translateY(0px) skewX(1deg)
    skewY(1deg);
}

.logo h1 {
  font-family: 'Lobster', cursive;
  font-size: 2.5rem;
  color: #37427f;
  text-align: center;
  -webkit-transform: scale(1) rotate(40deg) translateX(0px) translateY(0px)
    skewX(1deg) skewY(1deg);
  transform: scale(1) rotate(40deg) translateX(0px) translateY(0px) skewX(1deg)
    skewY(1deg);
}

.projects-heading {
  font-size: 2.5rem;
}

.levelDown {
  margin: 0 1rem;
  font-size: 1.5rem;
}

.right {
  background: #919190;
}

@media only screen and (min-width: 700px) {
  .App {
    display: flex;
  }

  .left {
    position: relative;
    align-items: center;
    width: 50%;
    height: 100vh;
    padding: 0;
  }

  .fixed {
    position: fixed;
  }

  .projects-heading {
    position: fixed;
    right: calc(50% - 130px);
    font-size: 3rem;
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
  }

  .levelDown {
    margin: 0 1rem;
    font-size: 2rem;
  }

  .right {
    width: 50%;
  }
}

